import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { RestProvider } from '../../../providers/rest/rest';
import { CallNumber } from '@ionic-native/call-number';
import { LoadingService } from "../../../providers/util/loading.service"



@IonicPage()
@Component({
  selector: 'page-listapolos',
  templateUrl: 'listapolos.html',
})
export class ListaPolosPage {
  polos: any;

  constructor(public loader:LoadingService, private callNumber: CallNumber, public navCtrl: NavController, public navParams: NavParams, public restProvider: RestProvider) {
    this.loader.presentWithMessage("Por favor, aguarde");
    
    this.loader.dismiss().then(() =>{
      this.getPolos();
    }); 
    
  }


  getPolos() {
    console.log("GETPOLOS");
    this.restProvider.getRest('{"operacao":"getpolos"}')
    .then(data => {
      console.log(data);
    this.polos = data;
    
    });
    }

  goToPolo(i){
      console.log(i);
      console.log(this.polos[i].AD0010UnidOrganicaNome);
      console.log(this.polos[i]);
      this.navCtrl.push('SinglepoloPage', {polo:this.polos[i]})

    }
    goToPesquisa(){      
      this.navCtrl.push('PesquisacursoPage')

    }


   callpolo(i){
 
      this.callNumber.callNumber(this.polos[i].AD0010UOTelefone, true)
      .then(res => console.log('Launched dialer! '+this.polos[i].AD0010UOTelefone, res))
      .catch(err => console.log('Error launching dialer ' + this.polos[i].AD0010UOTelefone, err));

    }
  ionViewDidLoad() {
    console.log('ionViewDidLoad ListapolosPage');
  }

}





